<template>
    <div class="storageOperation">
        <div class="bread_crumb">
            <!--<span>位置：主页 | </span>-->
            <span>当前位置：</span>
            <span>入库操作</span>
        </div>
        <div class="operation_title">
            <span>入库物品信息列表</span>
            <div class="search_input el-input el-input--suffix">
                <input autocomplete="off" v-model="operationInputValue" placeholder="请输入要搜索的物品名称" type="text" rows="2"
                       validateevent="true"
                       suffixicon="el-icon-search" class="p_search el-input__inner" @keyup.enter.active="searchOperationClick()">
                <span class="el-input__suffix" @click="searchOperationClick()">
                    <span class="el-input__suffix-inner">
                        <i class="el-input__icon iconfont el-icon-pharmaceutical_administration-sousuo"></i>
                    </span>
                </span>
            </div>
            <el-select class="select_input" v-model="operationOptionsValue" @change="changeOperationType()">
                <el-option v-for="item in operationSearchOptions" :key="item.value"
                           :label="item.label" :value="item.value">
                </el-option>
            </el-select>
            <el-button :class="operationSelection.length != 0 ? 'right_btn': 'disClick'" type="primary" @click="batchDelete()">批量删除</el-button>
            <el-button :class="operationSelection.length != 0 ? 'right_btn': 'disClick'" type="primary" @click="batchAddShelf()">批量上架</el-button>
            <!--<el-button class="right_btn" type="primary" @click="newStorage()">新增入库</el-button>-->
            <el-button class="right_btn" type="primary" @click="addBig()">新增大类</el-button>
            <!--连接设备  新增入库-->
            <el-button class="connect right_btn" @click="connectionClick()" type="primary" style="">新增入库</el-button>
        </div>
        <el-table class="operation_table" :data="operationData" stripe style="width: 100%"
                  tooltip-effect="dark" ref="operationTable" @selection-change="operationSelectionChange"
                  @select="checkSingle" @select-all="checkAll">
            <el-table-column type="selection" width="55">全选</el-table-column>
            <el-table-column prop="item_id" label="物品编号" width="100"></el-table-column>
            <el-table-column prop="frid_number" label="标签ID" width="100" show-overflow-tooltip></el-table-column>
            <el-table-column prop="item_name" label="物品名称" width="150"></el-table-column>
            <el-table-column prop="type" label="类型" width="120"></el-table-column>
            <el-table-column prop="sort_name" label="大类名称" width="100"></el-table-column>
            <el-table-column prop="dangerous" label="分类"></el-table-column>
            <el-table-column prop="stock" label="数量"></el-table-column>
            <el-table-column prop="unit" label="单位"></el-table-column>
            <el-table-column :label="operationOptionsValue == 1 ? '过期时间':'下次维护时间'" width="130">
                <template slot-scope="scope">
                    <span>{{ scope.row.overdue_at }}</span>
                </template>
            </el-table-column>
            <el-table-column prop="introduce" label="备注" width="200" show-overflow-tooltip></el-table-column>
            <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                    <span class="last operating" @click="editClick(scope.row)">编辑</span>
                    <span class="last">|</span>
                    <span class="last operating" @click="singleAddShelf(scope.row)">上架</span>
                </template>
            </el-table-column>
        </el-table>
        <div class="block" style="text-align: center;margin-top: 30px;">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="pageIndex"
                    :page-size="10"
                    layout="total, prev, pager, next, jumper"
                    :total="pageTotal">
            </el-pagination>
        </div>
        <!--新增入库-->
        <div class="mask_box" v-if="showNew">
            <div class="new_storage">
                <div class="new_title">
                    <span>新增入库</span>
                    <i class="el-icon-close" @click="closeNewBox()"></i>
                </div>
                <ul class="tabs_lists">
                    <li :class="newIndex == 1 ? 'stock_tabs_active' : ''" @click="goOne()">
                        试剂入库
                        <span></span>
                    </li>
                    <li :class="newIndex == 2 ? 'stock_tabs_active' : ''" @click="goTwo()">
                        仪器入库
                        <span></span>
                    </li>
                </ul>
                <div class="new_main">
                    <!--试剂入库--><!--仪器入库-->
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="名称" prop="item_name">
                            <el-input v-model="ruleForm.item_name"></el-input>
                        </el-form-item>
                        <div class="right_picture right_upload" :class="showOne ? 'show_one': ''">
                            <el-upload class="avatar-uploader" ref="upload" action="/api/item-edit"
                                       :show-file-list="true" accept="image/*"
                                       :on-success="handleAvatarSuccess"
                                       :on-error="handleAddError"
                                       :headers="mytoken" list-type="picture-card"
                                       :limit="1" :on-remove="handleRemove"
                                       :on-exceed="handleExceed"
                                       :on-change="handleChangePic" :data="uploadFile"
                                       :file-list="fileList33" :auto-upload="false"
                            >
                                <i class="el-icon-plus avatar-uploader-icon"></i>
                                <el-button class="up">上传图片</el-button>
                            </el-upload>
                            <div class="normalImg" v-if="!imageUrl">
                                <img v-if="newIndex == 1" src="../../static/pic_yaoji.png" class="avatar">
                                <img v-if="newIndex == 2" src="../../static/pic_yiqi.png" class="avatar">
                            </div>
                        </div>
                        <el-form-item label="大类" prop="sort_id">
                            <el-select  class="add_input" v-model="ruleForm.sort_id">
                                <el-option v-for="(item,index) in sortList" :key="item.id"
                                           :label="item.name" :value="item.id"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="入库数量" prop="stock">
                            <el-input v-model="ruleForm.stock"></el-input>
                        </el-form-item>
                        <el-form-item label="单位" prop="unit">
                            <el-select  class="add_input" v-model="ruleForm.unit">
                                <el-option v-for="(item,index) in unitList" :key="item.label"
                                           :label="item.label" :value="item.label"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="分类" prop="dangerous">
                            <el-select  class="add_input" v-model="ruleForm.dangerous">
                                <el-option v-for="(item,index) in dangerousList" :key="item.label"
                                           :label="item.label" :value="item.label"></el-option>
                            </el-select>
                        </el-form-item>
                        <!--试剂入库-->
                        <el-form-item v-if="newIndex == 1" class="time" label="过期时间">
                            <el-date-picker type="date" @change="onPickAddDate()"
                                            placeholder="" v-model="ruleForm.overdue_date"
                                            :picker-options="pickerOptions1">
                            </el-date-picker>
                        </el-form-item>
                        <!--仪器入库-->
                        <el-form-item v-if="newIndex == 2" class="time" label="下次维护时间">
                            <el-date-picker type="date" @change="onPickAddDate()"
                                            placeholder="" v-model="ruleForm.overdue_date"
                                            :picker-options="pickerOptions1"
                            ></el-date-picker>
                        </el-form-item>
                        <el-form-item class="label_id" label="电子标签ID" prop="frid_number">
                            <el-input v-model="ruleForm.frid_number" :readonly="is_read"></el-input>
                            <el-button type="primary" @click="scanningClick()">扫描绑定</el-button>
                        </el-form-item>
                        <el-form-item label="备注" prop="introduce">
                            <el-input type="textarea" resize="none" v-model="ruleForm.introduce"></el-input>
                        </el-form-item>
                    </el-form>
                    <div class="btn">
                        <el-button @click="submitUpload('ruleForm')">确认</el-button>
                    </div>
                </div>
            </div>
        </div>
        <!--扫描输入框-->
        <div class="scanning_box" v-if="showScanning">
            <div>
                <div class="scanning_title">
                    <span>请扫描</span>
                    <i class="el-icon-close" @click="closeScanningBox()"></i>
                </div>
                <el-input v-model="scanningValue" type="password" :maxlength="maxLength" autofocus></el-input>
                <div class="btn">
                    <el-button type="primary" @click="confimScanning()">确认</el-button>
                </div>
            </div>
        </div>
        <!--编辑-->
        <div class="mask_box" v-if="editStatus">
            <div class="edit_box">
                <div class="edit_title">
                    <span>编辑</span>
                    <i class="el-icon-close" @click="closeEditBox()"></i>
                </div>
                <div class="edit_main">
                    <el-form :model="editData" :rules="rules" ref="editData" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="名称" prop="item_name">
                            <el-input readonly v-model="editData.item_name"></el-input>
                        </el-form-item>
                        <div class="right_picture">
                            <el-upload class="avatar-uploader" ref="upload" action="/api/item-edit"
