                            <el-table-column type="selection" width="55">全选</el-table-column>
                            <el-table-column prop="item_id" label="物品编号" width="80"></el-table-column>
                            <el-table-column prop="item_name" label="物品名称"></el-table-column>
                            <el-table-column prop="frid_number" label="标签ID" width="100"  show-overflow-tooltip></el-table-column>
                            <el-table-column prop="sort_name" label="大类"></el-table-column>
                            <el-table-column prop="stock" label="数量" width="100"></el-table-column>
                            <el-table-column prop="unit" label="单位"></el-table-column>
                            <el-table-column prop="address" label="上架位置"></el-table-column>
                        </el-table>
                    </div>
                    <div class="btn">
                        <el-button :class="batchShelfAddress ? '' : 'unClick'" @click="batchConfirmShelf()">确认上架</el-button>
                        <!--<el-button v-if="batchShelfAddress" @click="batchConfirmShelf()">确认上架</el-button>-->
                        <!--<el-button v-if="!batchShelfAddress" disabled>确认上架</el-button>-->
                    </div>
                </div>
            </div>
        </div>
