//                console.log(row);
                let data = true;    //勾选数据
                let index = '';     //数据下标
                for (let x in this.storageSelectData) {
                    if (this.storageSelectData[x].item_id == row.item_id) {
                        data = false;
                        index = x;  //勾选取消
                    }
                }
                if (data) {
                    this.storageSelectData.push(row);
                } else {
                    this.storageSelectData.splice(index, 1);
                }
                this.operationSelection = this.storageSelectData;
            },
            // 表格多选框
            checkAll(selection) {
                if (selection.length == 0) {
                    //取消
                    for (let i in this.operationData) {
                        for (let x in this.storageSelectData) {
                            if (this.operationData[i].item_id == this.storageSelectData[x].item_id) {
                                this.storageSelectData.splice(x, 1);
                            }
                        }
                    }
                    this.operationSelection = this.storageSelectData;
                } else {
                    //全选当前页
                    if (this.storageSelectData.length == 0) {
                        //如果没有被选中的
                        this.storageSelectData = selection;
                    } else {
                        for (let i in selection) {
                            this.storageSelectData.push(selection[i])
                        }
                    }
                    this.storageSelectData = this.unique(this.storageSelectData);
                    this.operationSelection = this.storageSelectData;
                }
            },
            //对象数组去重
            unique(array) {
                var obj = {};
                return array.filter(function (item, index, array) {
                    //console.log(typeof item + JSON.stringify(item))
                    return obj.hasOwnProperty(typeof item + JSON.stringify(item)) ? false : (obj[typeof item + JSON.stringify(item)] = true)
                })
            },
            //单个上架
            singleAddShelf(val) {
//				console.log("单个");
//				console.log(val);
                this.shelfType = 'single';
                this.singleShelfIndex = val.item_id;        //单个物品id
                this.selectIndex = 0;
                this.shelfMapStatus = true;
                this.selectLocation = this.pointData[0];
            },
            //选择某个位置
            chooseLocationClick(point, val) {
                this.selectIndex = val;
                this.selectLocation = point;
//				console.log(point,val)
                //获取侧边栏进行滚动
                var div = document.getElementById("sideBox");
//				console.log(div)
//				console.log(div.scrollHeight);//总高度
//				div.scrollTop = div.scrollHeight;//到底部
                //前11个
                if (val < 11) {
                    div.scrollTop = 0;
                } else if (val >= 11 && val < 22) {
                    div.scrollTop = 429;
                } else if (val >= 22) {
                    div.scrollTop = 429 * 2;
                }
            },
            //侧边栏选择某个位置
            chooseLocationRightClick(point, val) {
                this.selectIndex = val;
                this.selectLocation = point;
            },
            //确认上架位置
            confirmShelfLocation() {
                //获取选择的位置
                let address = "POINT(" + ((parseInt(this.selectLocation.x) + 15) / 1200).toFixed(3) + " " + ((parseInt(this.selectLocation.y) + 30) / 900).toFixed(3) + ")";
                if (this.shelfType == 'single') {
                    //单个
                    let arr = [
                        {
                            item_id: this.singleShelfIndex,
                            situation: address,
                            s_content: this.selectLocation.name
                        },
                    ];
                    this.$axios.post('/item-is', {
                        item: arr
                    }).then((response) => {
//                    	console.log(response)
                        if (response.data.status == '0') {
                            this.handleCurrentChange(1);
                            this.$message({
                                message: '上架成功！',
                                type: 'success',
                                duration: '1000',
                            });
                            //清空多选项
                            this.operationSelection = this.storageSelectData = [];
                        } else {
                            this.$message({
                                message: '上架失败！',
                                type: 'warning',
                                duration: '1000',
                            });
                        }
                    }).catch((error) => {
                    })
                } else if (this.shelfType == 'lot') {
                    //批量//渲染数据 上架地址
                    for (let i in this.batchShelfData) {
                        this.batchShelfData[i].address = this.pointData[this.selectIndex].name;
                        this.batchShelfAddress = this.pointData[this.selectIndex];
                    }
                }
                this.shelfMapStatus = false;
            },
            //对象数组排序
            compare(property) {
                return function (obj1, obj2) {
                    var value1 = obj1[property];
                    var value2 = obj2[property];
                    return value1 - value2;     // 升序
                }
            },
            //批量上架按钮
            batchAddShelf() {
//                console.log(this.operationSelection)
                if (this.operationSelection.length == 0) {
//                    this.$message({
//                        message: '请先选择要上架的物品！',
//                        type: 'warning',
//                        duration: '1000',
//                    });
                } else {
                    this.shelfType = 'lot';
                    this.addShelfStatus = true;
                    this.batchShelfData = this.operationSelection.sort(this.compare("item_id"));
                }
            },
            //批量上架 选择上架位置 打开地图
            chooseShelfLocation() {
                this.shelfMapStatus = true;
                this.selectIndex = 0;
                this.selectLocation = this.pointData[0];
            },
            //选择上架地址后 改变数据样式
            tableRowClassName({row, rowIndex}) {
                if (this.batchShelfAddress) {
//					return '';
                }
            },
            //最终批量多选的数据
            handleSelectionChange(val) {
//				console.log(val)
                this.batchLastData = val;
            },
            //批量确认上架
            batchConfirmShelf() {
//				console.log("批量确认")
                //最终选取要上架的物品
                if (this.batchLastData.length == 0) {
                    this.$message({
                        message: '请先选择要勾选上架的物品！',
                        type: 'warning',
                        duration: '2000',
                    });
                } else {
                    let address = "POINT(" + ((parseInt(this.selectLocation.x) + 15) / 1200).toFixed(3) + " " + ((parseInt(this.selectLocation.y) + 30) / 900).toFixed(3) + ")";
                    let arr = [];
                    for (let i in this.batchLastData) {   //batchShelfData
                        if (this.batchLastData[i].address == undefined) {
                        } else {
                            arr.push({
                                item_id: this.batchLastData[i].item_id,
                                situation: address,
                                s_content: this.selectLocation.name
                            })
                        }
                    }
                    this.$axios.post('/item-is', {
                        item: arr
                    }).then((response) => {
                        //                    	console.log(response)
                        if (response.data.status == '0') {
                            this.handleCurrentChange(1);
                            this.$message({
                                message: '上架成功！',
                                type: 'success',
                                duration: '1500',
                            });
                            //清空可以批量上架的数量和位置
                            this.addShelfStatus = false;
                            this.batchShelfAddress = '';
                            this.batchShelfData = [];
                            for (let i in this.operationSelection) {
                                if (i < this.operationSelection) {
                                    this.operationSelection[i].address = '';
                                }
                            }
                            this.operationSelection = this.storageSelectData = [];
                        } else {
                            this.$message({
                                message: '上架失败！',
                                type: 'warning',
                                duration: '1500',
                            });
                        }
                    }).catch((error) => {
                    })
                }
            },
            //关闭批量上架的框
            closeBatchBox() {
                this.addShelfStatus = false;
                //清空可以批量上架的数量和位置
                this.batchShelfData = [];
                this.batchShelfAddress = '';
                for (let i in this.operationSelection) {
                    if (i < this.operationSelection) {
                        this.operationSelection[i].address = '';
                    }
                }
            },
            //关闭上架地图
            closeShelfMap() {
                this.shelfMapStatus = false;
            },
            //批量删除
            batchDelete() {
                if (this.operationSelection.length == 0) {
//                    this.$message({
//                        type: 'warning',
//                        message: '请先选择要删除的物品!',
//                        duration: '1500',
//                    });
                } else {
                    let arr = [];
                    for (let i in this.operationSelection) {   //batchShelfData
                        arr.push({
                            item_id: this.operationSelection[i].item_id,
                        })
                    }
                    this.$confirm('此操作将永久删除被选中的数据, 是否继续?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.$axios.post('/item-del', {
                            item: arr
                        }).then((response) => {
//							console.log(response)
                            if (response.data.status == 0) {
                                this.$message({
                                    type: 'success',
                                    message: '删除成功!',
                                    duration: '1500',
                                });
                                this.operationSelection = this.storageSelectData = [];
                                this.handleCurrentChange(1);
                            }
                        }).catch((error) => {
                        })
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消删除',
                            duration: '1500',
                        });
                    });
                }
            },
            //分页
            handleCurrentChange(val) {
//				console.log(`当前页: ${val}`);
                this.pageIndex = val;
                let item_type = '';
                if (this.operationOptionsValue == 1) {
                    item_type = '药品试剂';
                } else if (this.operationOptionsValue == 2) {
                    item_type = '仪器设备';
                }
                ;
                this.$axios.post('/item-list', {
                    item_type: item_type,
                    number: '10',
                    page: val,
                    item_name: this.operationInputValue
                }).then((response) => {
                    let showList = [];  //当前页被选中的数据
//                    console.log(response)
                    if (response.data.data) {
                        this.pageTotal = response.data.data.total;
                        this.operationData = response.data.data.data;
                        //获取当前页被选中的
                        for (let x in this.operationData) {
                            for (let y in this.storageSelectData) {
                                if (this.operationData[x].item_id == this.storageSelectData[y].item_id) {
                                    showList.push(this.operationData[x])
                                }
                            }
                        }
                        this.$nextTick(function () {
                            this.checkedChoose(showList);
                        });
                    } else {
                        this.pageTotal = 0;
                        this.operationData = [];
                    }
                })
            },
            //勾选被选中的
            checkedChoose(rows) {
                rows.forEach(row => {
                    this.$refs.operationTable.toggleRowSelection(row, true);
                });
            },
        },
        filters: {
            //到期时间(下次维护时间)
            over_date: function (value) {
                if (value) {
                    return value.split(' ')[0];
                } else {
                    return '';
                }
            },
        },
    }
</script>

