                    padding: 8px 20px;
                }
            }
        }
        //编辑
        .edit_box {
            width: 784.1px;
            height: 650px;
            margin-bottom: 20px;
            object-fit: contain;
            background-color: #ffffff;
            box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.5);
            position: absolute;
            top: 193px;
            left: 50%;
            margin-left: -392px;
            div.edit_title {
                width: 784.1px;
                height: 60px;
                line-height: 60px;
                text-align: center;
                background-color: #5ca8e7;
                position: relative;
                color: #fff;
                span {
                    font-family: PingFangSC;
                    font-size: 20px;
                    color: #ffffff;
                }
                i {
                    font-size: 27px;
                    font-weight: 600;
                    cursor: pointer;
                    position: absolute;
                    right: 15px;
                    top: 15px;
                }
            }
            .edit_main {
                .el-form.demo-ruleForm {
                    margin-top: 36px;
                    margin-left: 36px;
                    .el-form-item {
                        display: inline-block;
                        width: 400px;
                        .el-form-item__label {
                            width: 90px !important;
                            height: 17px;
                            font-family: PingFangSC;
                            font-size: 12px;
                            text-align: right;
                            color: #000000;
                            &:before {
                                content: normal;
                            }
                        }
                        .el-input {
                            width: 239.1px;
                            height: 24.5px;
                            border-radius: 2px;
                            .el-input__inner {
                                height: 24.5px;
                                line-height: 24.5px;
                                background-color: #cfe2f3;
                                font-family: PingFangSC;
                                font-size: 12px;
                                text-align: left;
                                color: #4a4a4a;
                                border: 0;
                                border-radius: 2px;
                            }
                        }
                        .el-form-item__content {
                            line-height: 24.5px;
                            margin-left: 120px !important;
                        }
                        .el-form-item__label {
                            line-height: 24.5px;
                        }
                        .el-select .el-input .el-select__caret {
                            color: #223241;
                        }
                        .el-button {
                            width: 92px;
                            height: 24px;
                            padding: 0;
                            font-family: PingFangSC;
                            font-size: 12px;
                            text-align: center;
                            color: #ffffff;
                            border-radius: 100px;
                            background-color: #5ca8e7;
                            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.5);
                        }
                        .el-textarea {
                            width: 239.1px;
                            border-radius: 2px;
                            .el-textarea__inner {
                                height: 82.8px;
                                background-color: #cfe2f3;
                            }
                        }
                    }
                    .right_picture {
                        float: right;
                        width: 219.2px;
                        margin-right: 98.8px;
                        text-align: center;
                        .avatar-uploader {
                            width: 219.2px;
                            height: 270px;
                            /*line-height: 270px;*/
                            border: solid 2px #bddcf5;
                            box-sizing: border-box;
                            .el-upload {
                                width: 100%;
                                height: 100%;
                                font-size: 28px;
                                color: #bddcf5;
                                font-weight: 600 !important;
                                position: relative;
                                .el-button {
                                    color: #fff;
                                    padding: 0;
                                    width: 109.9px;
                                    height: 30px;
                                    border-radius: 22px;
                                    background-color: #5ca8e7;
                                    border: solid 4px rgba(92, 168, 231, 0.35);
                                    position: absolute;
                                    bottom: 0;
                                    left: 50%;
                                    margin-left: -55px;
                                    margin-bottom: -50px;
                                }
                            }
                        }
                        .el-upload-list {
                            margin-top: 60px;
                        }
                        img {
                            width: 100%;
                            height: 266px;
                        }
                    }
                    .time {
                        .el-input__inner {
                            padding-left: 15px;
                        }
                        .el-input__prefix i {
                            line-height: 24.5px;
                            cursor: pointer;
                            width: 26px;
                            height: 24.5px;
                            color: white;
                            border-radius: 0 4px 4px 0px;
                            background-color: #5ca8e7;
                            position: relative;
                            left: 208px;
                        }
                        .el-input__suffix {
                            left: 155px;
                            i.el-input__icon {
                                line-height: 100% !important;
                            }
                        }
                    }
                    .label_id {
                        .el-input {
                            margin-right: 5px;
                            /*width: 139.1px;*/
                            padding-right: 10px;
                        }
                        .el-button {
                            margin-top: 10px;
                        }
                    }
                }
                .btn {
                    text-align: center;
                    margin-top: 35px;
                    .el-button {
                        border: 0;
                        padding: 0;
                        color: #fff;
                        width: 172px;
                        height: 42px;
                        border-radius: 5px;
                        background-color: #5ca8e7;
                        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
                    }
                }
            }
        }
        //上架
        .add_shelf {
            width: 855.2px;
            height: 626.2px;
            object-fit: contain;
            background-color: #ffffff;
            box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.5);
            position: absolute;
            top: 193px;
            left: 50%;
            margin-left: -427.6px;
            div.add_title {
                width: 855.2px;
                height: 60px;
                line-height: 60px;
                text-align: center;
                background-color: #5ca8e7;
                position: relative;
                color: #fff;
                span {
                    font-family: PingFangSC;
                    font-size: 20px;
                    color: #ffffff;
                }
                i {
                    font-size: 27px;
                    font-weight: 600;
                    cursor: pointer;
                    position: absolute;
                    right: 15px;
                    top: 15px;
                }
            }
            .add_main {
                .main_header {
                    position: relative;
                    height: 52px;
                    line-height: 52px;
                    text-align: center;
                    > span {
                        font-family: PingFangSC;
                        font-size: 16px;
                        text-align: center;
                        color: #4a4a4a;
                    }
                    div {
                        font-family: PingFangSC;
                        font-size: 16px;
                        text-align: left;
                        color: #4a4a4a;
                        height: 52px;
                        line-height: 52px;
                        position: absolute;
                        top: 0;
                        right: 15px;
                        cursor: pointer;
                        &:before {
                            color: #5abfac;
                            font-size: 24px;
                        }
                        span {
                            float: right;
                            height: 52px;
                            line-height: 52px;
                        }
                    }
                }
                .data {
                    .data_table {
                        width: 821.1px !important;
                        background-color: #ffffff;
                        margin: 0 auto;
                        border: solid 1px #979797;
                    }
                }
                .btn {
                    text-align: center;
                    margin-top: 59px;
                    .el-button {
                        padding: 0;
                        color: #fff;
                        width: 172px;
                        height: 42px;
                        border-radius: 5px;
                        background-color: #5ca8e7;
                        border: 1px solid #5ca8e7;
                        font-size: 14px;
                        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
                    }
                    .unClick {
                        padding: 0;
                        color: #fff;
                        width: 172px;
                        height: 42px;
                        border-radius: 5px;
                        background-color: #909399;
                        border: 1px solid #909399;
                        font-size: 14px;
                        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
                    }
                    .unClick:active {
                        border: 1px solid #909399;
                    }
                }
            }
        }
        //上架地图
        .map_box {
            position: fixed;
            top: 0;
            left: 0;
            z-index: 999;
            width: 100%;
            height: 100%;
            overflow: auto;
            background-color: rgba(0, 0, 0, 0.15);
            .shelf_map {
                width: 1330.8px;
                object-fit: contain;
                background-color: #ffffff;
                box-shadow: 0 3px 8px 0 rgba(0, 0, 0, 0.5);
                position: absolute;
                top: 150px;
                left: 50%;
                margin-left: -665.4px;
                .map_title {
                    width: 1330.8px;
                    height: 60px;
                    line-height: 60px;
                    text-align: center;
                    background-color: #5ca8e7;
                    position: relative;
                    color: #fff;
                    span {
                        font-size: 20px;
                    }
                    i {
                        font-size: 27px;
                        font-weight: 600;
                        cursor: pointer;
                        position: absolute;
                        right: 36.5px;
                        top: 15px;
                    }
                }
                .tip {
                    position: absolute;
                    top: 77px;
                    left: 20.3px;
                    z-index: 111;
                    span {
                        width: 103.5px;
                        height: 22px;
                        font-family: PingFangSC;
                        font-size: 16px;
                        text-align: left;
                        color: #4a4a4a;
                    }
                    .address {
                        color: #f05050;
                    }
                }
                //右侧栏
                .right_list {
                    position: absolute;
                    right: 13px;
                    top: 131px;
                    z-index: 111;
                    height: 429px;
                    width: 160px;
                    overflow: auto;
                    ul {
                        li {
                            padding-left: 10px;
                            height: 38px;
                            line-height: 38px;
                            background-color: #5abfac;
                            color: #fff;
                            border-bottom: 1px solid #fff;
                            font-size: 16px;
                            cursor: pointer;
                            span {
                                display: none;
                            }
                        }
                        .is_active {
                            background-color: #f05050;
                            span {
                                display: inline-block;
                                &:before {

                                }
                            }
                        }
                    }
                }
                .map_main {
                    width: 100%;
                    max-height: 600px;
                    position: relative;
                    overflow: auto;
                    img {
                        width: 1200px;
                    }
                    //点
                    .point_list {
                        width: 1200px;
                        height: 900px;
                        position: absolute;
                        top: 0;
                        left: 0;
                        li {
                            cursor: pointer;
                            font-size: 30px;
                            color: #5ca8e7;
                            div {
                                width: 152.9px;
                                padding-left: 15px;
                                height: 41px;
                                line-height: 41px;
                                background-color: #ffffff;
                                box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.5);
                                font-family: PingFangSC;
                                font-size: 16px;
                                text-align: left;
                                color: #4a4a4a;
                                float: right;
                                margin-top: -13px;
                                border-radius: 5px;
                                position: relative;
                                z-index: 444;
                                margin-left: 5px;
                                span {
                                    position: absolute;
                                    top: 50%;
                                    left: 0;
                                    width: 10px;
                                    height: 10px;
                                    margin-left: -5px;
                                    margin-top: -5px;
                                    background-color: #fff;
                                    transform: rotate(45deg);
                                    -ms-transform: rotate(45deg);
                                    -moz-transform: rotate(45deg);
                                    -webkit-transform: rotate(45deg);
                                    -o-transform: rotate(45deg);
                                }
                            }
                        }
                        .active {
                            color: #b42b19;
                        }
                        li:hover {
                            div {
                                display: block !important;
                            }
                        }
                    }
                    .btn {
                        text-align: center;
                        margin-bottom: 12px;
                        .el-button {
                            color: #fff;
                            border-radius: 5px;
                            background-color: #5ca8e7;
                            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
                        }
                    }
                }
            }
        }
    }
</style>



